import {Component, OnInit, Output, EventEmitter, Input} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MyErrorStateMatcher} from 'shared/shared-functions/state-matcher';
import {ISharedUserTypes} from 'shared/shared-interfaces/user-types';
import {UserTypesData} from 'shared/shared-static-data/userType';
import {AddressType} from './../../../shared-static-data/addressType';
import {ISharedAdressTypes} from './../../../shared-interfaces/address-types';
import {ISharedPhoneTypes} from './../../../shared-interfaces/phone-types';
import {PhoneTypes} from 'shared/shared-static-data/phoneTypes';
import {ISharedLeadSourceTypes} from 'shared/shared-interfaces/lead-source-types';
import {Months} from 'shared/shared-static-data/months';
import {DateOfYearService} from 'shared/shared-services/date-of-year.service';
import {MustMatch} from 'shared/shared-functions/MustMatch';
import {ISharedRegister} from 'shared/shared-interfaces/register';
import {ISharedPaymentTypes} from 'shared/shared-interfaces/payment-types';
import {PaymentTypes} from 'shared/shared-static-data/paymentTypes';
import {LeadSourceTypes} from 'shared/shared-static-data/leadSourceTypes';
import {ISharedCountry} from 'shared/shared-interfaces/country';
import {Countries} from 'shared/shared-static-data/Countries';
import {ISharedStates} from './../../../shared-interfaces/states';
import {States} from 'shared/shared-static-data/States';
import {
  NoteMustHaveCreditCardComponent
} from 'shared/shared-components/shard-popup/note-must-have-credit-card/note-must-have-credit-card.component';
import {MatDialog} from '@angular/material/dialog';
import {NoteMustHaveStatesComponent} from './../../shard-popup/note-must-have-states/note-must-have-states.component';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';
import {NavigateToService} from '../../../shared-services/navigate-to.service';
import {NavigateToEnum} from '../../../shared-enums/NavigateToEnum';
import { ForgetPasswordComponent } from 'shared/shared-components/forget-password/forget-password.component';
import {PasswordValidators} from '../../../shared-validators/password-validator/password.validator';

/**
 * Copyright (c) 3ddx
 *
 * @summary Register Form Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-12-30
 */
@Component({
  selector: 'app-register-form',
  templateUrl: './register-form.component.html',
  styleUrls: ['./register-form.component.css']
})
export class RegisterFormComponent implements OnInit {
  registerInfoFormGroup!: FormGroup;
  personalInfoFormGroup!: FormGroup;
  additionalInfoFormGroup!: FormGroup;
  billingInfoFormGroup!: FormGroup;
  matcher!: MyErrorStateMatcher;
  paymentType: number = 1000;
  countryId!: number;
  registerIsValid!: boolean;
  isEditable: boolean = true;
  hide: boolean = true;
  submitted: boolean = false;
  UserTypes: Array<ISharedUserTypes> = UserTypesData;
  AddressType: Array<ISharedAdressTypes> = AddressType;
  PhoneTypes: Array<ISharedPhoneTypes> = PhoneTypes;
  LeadSourceTypes: Array<ISharedLeadSourceTypes> = LeadSourceTypes;
  PaymentTypes: Array<ISharedPaymentTypes> = PaymentTypes;
  Months: Array<string> = Months;
  Country: Array<ISharedCountry> = Countries;
  State: Array<ISharedStates> = States;

  payment: string = '/assets/Images/Visa.gif';
  Years!: Array<number>;
  @Output() registerInOBJ: EventEmitter<ISharedRegister> = new EventEmitter<ISharedRegister>();
  @Output() forgetPasswordOBJ: EventEmitter<{ email: string }> = new EventEmitter();
  @Input() userData!: any;
  @Input() logo: string = '';
  @Input() isAlreadyExist:boolean=false;
  htmlID = CStaticIds;
  urlNavigations = NavigateToEnum;

  /**
   * The Requirements For The Construction Of The Component
   * @param dateOfYearService for using DateOfYearService
   * @param fb for using FormBuilder
   * @param dialog for using angular material dialog
   * @param navigateToService for using Navigations
   */
  constructor(
    private fb: FormBuilder,
    private dateOfYearService: DateOfYearService,
    private dialog: MatDialog,
    public navigateToService: NavigateToService,
  ) {
  }

  /**
   * This function creat phone form group
   * @return FormGroup
   */
  createPhone(): FormGroup {
    return this.fb.group({
      telecom: ['', Validators.required],
      telecomEnumId: ['', Validators.required]
    });
  }

  ngOnInit(): void {
    this.Years = this.dateOfYearService.generateYearsList(20);

    this.createRegisterInfoForm();
    this.createPersonalInfoForm();
    this.createAdditionalInfoForm();
    this.createBillingInfoForm();
    this.getPaymentType();
    this.getCountryId();
    this.matcher = new MyErrorStateMatcher();
  }

  /**
   * This function creat register info form group
   * And matching between password and confirm password
   * @return void
   */
  createRegisterInfoForm(): void {
    this.registerInfoFormGroup = this.fb.group({
        userType: [2, Validators.required],
        email: ['', [Validators.required, Validators.email]],
        password: ['', [Validators.required,
          Validators.minLength(8),
          PasswordValidators.hasDigit(),
          PasswordValidators.hasUpper(),
          PasswordValidators.hasLower(),
          PasswordValidators.hasSpecialChar()]],
        confirmPassword: ['', Validators.required]
      },
      {validator: MustMatch('password', 'confirmPassword')}
    );
  }

  /**
   * This function creat personal info form group
   * @return void
   */
  createPersonalInfoForm(): void {
    this.personalInfoFormGroup = this.fb.group({
      fName: ['', Validators.required],
      lName: ['', Validators.required],
      address1: ['', Validators.required],
      address2: [''],
      addressType: ['', Validators.required],
      city: [''],
      country: ['', [Validators.required]],
      state: ['', [Validators.required]],
      zipCode: [''],
      phones: this.fb.array([this.createPhone()])
    });
  }

  /**
   * This function creat additional info form group
   * @return void
   */
  createAdditionalInfoForm(): void {
    this.additionalInfoFormGroup = this.fb.group({
      leadSource: [''],
      pleaseSpecify: [''],
      pleaseSpecifyName: [''],
    });
  }

  /**
   * This function creat billing info form group
   * @return void
   */
  createBillingInfoForm(): void {
    this.billingInfoFormGroup = this.fb.group({
      paymentType: [1000, Validators.required],
      creditCardNum: [{value: '', disabled: true}, Validators.required],
      month: [{value: '', disabled: true}, Validators.required],
      year: [{value: '', disabled: true}, Validators.required],
      rcivMail: false,
      privacyPolicy: false,
      termsOfService: false
    });
  }

  /**
   * This function return phones control from personalInfoFormGroup group
   * @return FormArray
   */
  get phones(): FormArray {
    return this.personalInfoFormGroup.get('phones') as FormArray;
  }

  /**
   * This function push phone group to phones array
   * @return void
   */
  addPhones(): void {
    this.phones.push(this.createPhone());
  }

  /**
   * This function remove phone group by index
   * @return void
   * @param {number} index
   */
  removePhones(index: number): void {
    this.phones.removeAt(index);
  }

  /**
   * This function check states (privacy policy - terms of service)
   * @return boolean
   */
  checkStatesUser(): boolean {
    if (!this.billingInfoFormGroup.controls.privacyPolicy.value ||
      !this.billingInfoFormGroup.controls.termsOfService.value) {
      this.dialog.open(NoteMustHaveStatesComponent, {
        disableClose: true,
        data: {
          termsOfService: this.billingInfoFormGroup.controls.termsOfService,
          privacyPolicy: this.billingInfoFormGroup.controls.privacyPolicy
        }
      });
      return false;
    }
    return true;
  }

  /**
   * This function get payment type value
   * check value if value equal 1000 : (creditCardNum - month - year) => disabled
   * @return void
   */
  getPaymentType(): void {
    this.billingInfoFormGroup.controls.paymentType.valueChanges.subscribe(res => {
      this.paymentType = res;
      if (this.paymentType === 1000) {
        this.billingInfoFormGroup.controls.creditCardNum.disable();
        this.billingInfoFormGroup.controls.month.disable();
        this.billingInfoFormGroup.controls.year.disable();
      } else {
        this.billingInfoFormGroup.controls.creditCardNum.enable();
        this.billingInfoFormGroup.controls.month.enable();
        this.billingInfoFormGroup.controls.year.enable();
      }
    });
  }

  /**
   * This function get country id
   * @return void
   */
  getCountryId(): void {
    this.personalInfoFormGroup.controls.country.valueChanges.subscribe(res => {
      this.countryId = res;
    });
  }

  /**
   * This function emit four forms to registerInOBJ
   * @return void
   */
  emitDataUser(): void {
    this.registerInOBJ.emit({
      ...this.registerInfoFormGroup.value,
      ...this.personalInfoFormGroup.value,
      ...this.additionalInfoFormGroup.value,
      ...this.billingInfoFormGroup.value,
    });
  }

  /**
   * This function submit form and output data to sign up component
   *
   * @return void
   */
  onSubmit(): void {
    this.submitted = true;
    this.registerIsValid = (
      this.registerInfoFormGroup.valid &&
      this.personalInfoFormGroup.valid &&
      this.additionalInfoFormGroup.valid &&
      this.billingInfoFormGroup.valid
    );

    if (this.registerIsValid) {
      if (this.checkStatesUser()) {
        if (this.paymentType === 1000) {
          const dialogRef = this.dialog.open(NoteMustHaveCreditCardComponent, {
            disableClose: true
          });
          dialogRef.afterClosed().subscribe(result => {
            // tslint:disable-next-line:no-unused-expression
            result.event === 'Ok' ? this.emitDataUser() : null;
          });
        } else {
          this.emitDataUser();
        }
      }
    }
  }

  onForgetPassword() {
    const dialogRef = this.dialog.open(ForgetPasswordComponent, {
      minWidth: '40vw',
      maxWidth: '80vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo,
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.forgetPasswordOBJ.emit(result);
      }
    });

    return false;
  }
}
