import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { CStaticIds } from '../../../../shared-static-id/shared-static-id';
import {DatePipe} from '@angular/common';
import {OrderSharedService} from '../../../../shared-services/order-shared.service';

@Component({
	selector: 'app-radiology-report-form',
	templateUrl: './radiology-report-form.component.html',
	styleUrls: ['./radiology-report-form.component.css']
})
export class RadiologyReportFormComponent implements OnInit {
	@Input() radiologyReport!: FormGroup;
	@Output() changeValue: EventEmitter<Function> = new EventEmitter<Function>();
	rushprice: any
	currency: any
	htmlID = CStaticIds;
	minDate!:Date;
	maxDate!:Date;
  maxTransformedDate!: string | any;
  minTransformedDate!: string | any;
	constructor(private datePipe: DatePipe, public orderService: OrderSharedService) {
		this.maxDate = new Date();
		this.minDate=new Date(this.maxDate.getFullYear()-100,this.maxDate.getMonth(),this.maxDate.getDate());
  this.maxTransformedDate = this.datePipe.transform(this.maxDate, 'yyyy-MM-dd');
  this.minTransformedDate = this.datePipe.transform(this.minDate, 'yyyy-MM-dd');
	 }

	ngOnInit(): void {
		this.rushprice = localStorage.getItem('reportRushPrice');
		this.currency = localStorage.getItem('userCurrencySymbol');
		this.getBasicReportValue();
	}

  getBasicReportValue(): void {
		this.radiologyReport.controls.BasicReport.valueChanges.subscribe(res => {
			if (res === 2) {
				this.radiologyReport.controls.implantPlanned.disable();
				this.radiologyReport.controls.evaluateExistingImplant.disable();
				this.radiologyReport.controls.sinusEvaluation.disable();
				this.radiologyReport.controls.tmjevaluation.disable();
				this.radiologyReport.controls.ruleOutPathology.disable();
				this.radiologyReport.controls.airWayEvaluation.disable();
				this.radiologyReport.controls.airWayEvaluation.disable();
			} else {
				this.radiologyReport.controls.implantPlanned.enable();
				this.radiologyReport.controls.evaluateExistingImplant.enable();
				this.radiologyReport.controls.sinusEvaluation.enable();
				this.radiologyReport.controls.tmjevaluation.enable();
				this.radiologyReport.controls.ruleOutPathology.enable();
				this.radiologyReport.controls.airWayEvaluation.enable();
				this.radiologyReport.controls.airWayEvaluation.enable();
			}
		})
	}

  validateDOBDates(): void {
    this.orderService.checkMaxDOB = new Date(this.radiologyReport.controls.birthdate.value) > new Date(this.maxTransformedDate);
    this.orderService.checkMinDOB = new Date(this.radiologyReport.controls.birthdate.value) < new Date(this.minTransformedDate);
  }
  validateDOI(): void{
    this.orderService.checkMinDOI = new Date(this.radiologyReport.controls.imagedate.value) < new Date(this.minTransformedDate);
  }
}
